.submit-button {
  // display: flex;
  // flex-direction: row;
  // align-items: center;
  // justify-content: space-evenly;
  margin-bottom: 5px;
  padding: 5px 32px;
  border-radius: 5px;
  color: #fff;
  width: 90%;
  height: 5vh;
  // max-width: 300px;
  font-size: 1.2rem;
  letter-spacing: .1rem;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  z-index: 1;
  border: none;

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $olivine;
    border-radius: 5px;    
    z-index: -2;
  }

  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: darken($olivine, 15%);
    transition: all .3s;
    border-radius: 5px;    
    z-index: -1;
  }

  &:hover {
    color: white;

  &:before {
    width: 100%;
    }
  }
}

